/*-----Start Scroll go up-----*/
#btn__scrollToTop {
    @include dl__flex;
    height: 2.5rem;
    width: 2.5rem;
    bottom: 1rem;
    right: 1rem;
    position: fixed;
    background: $color__success;
    color: $color__coral;
    cursor: pointer;
    border-radius: 2.5rem;
    font-size: 1.5rem;
    outline: none;
    transition: all 0.4s linear;
    box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;

    &:hover {
        transition: all 0.5s linear;
        color: $color__coral;
        box-shadow: $boxShadow__primary;
        background: $color__success;
    }
}

/*-----End Scroll go up-----*/